var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('article-outer',[_c('article-title',[_vm._v("【接案諮詢案例】平面設計師轉為自由工作者：面對多重可能的選擇焦慮")]),_vm._v(" "),_c('article-tag',{attrs:{"date":'2025.05.30',"tag":'接案．設計師',"link":'/articles'}}),_vm._v(" "),_c('article-img',{attrs:{"link":require('@/assets/img/articles/case-study-graphic-designer/1.jpg'),"alt":"【接案諮詢案例】平面設計師轉為自由工作者"}}),_vm._v(" "),_c('article-sub',[_vm._v("個案狀況分析")]),_vm._v(" "),_c('article-paragraph',[_vm._v("\n    一位平面設計師曾在藝文產業與企業中接案，累積了豐富的自由工作經驗。原先計畫轉職全職職場，但因緣際會下再次回到自由工作者身分，目前同時接有短期兼職與案子。只是，相較於過去的「有案就接」，這次他停了下來，開始思考：「現在的自己，到底適合怎麼樣的工作與生活方式？」"),_c('br'),_c('br'),_vm._v("\n    他的困惑不只來自職涯的選擇，更來自對「動力」的重新定義。過去，他常被推著前進，靠的是經濟需求或社會期待。而如今，他有了更完整掌握時間的自由，也開始感受到一種前所未有的身心健康。只是，在這樣「不再仰賴外在動力」的階段，也開始擔心——是否正在錯過什麼？"),_c('br'),_c('br'),_vm._v("\n    他提到自己對多元領域有興趣，偏好創作、表達等互動型工作，但當進入具體執行（例如經營自媒體、策劃內容）時，卻又難以產生持續動力。也嘗試分析不同工作的好壞，卻仍卡在「不知道該走哪條路」的焦慮中。"),_c('br'),_c('br'),_vm._v("\n    他手中有一些已知元素、一些模糊邊界，也意識到自己有更多可能性，但尚未拼出一幅完整圖像。而他的焦慮，並非來自「沒有方向」，而是「可能性太多，無法篩選」。\n  ")]),_vm._v(" "),_c('article-sub',[_vm._v("個案喜歡的地方")]),_vm._v(" "),_c('article-paragraph',[_vm._v("\n    這次和"),_c('a',{attrs:{"href":"https://siddharam.com/counsel","target":"_blank"}},[_vm._v("西打諮詢")]),_vm._v("，他特別喜歡提問方式帶來的思考空間——不是提供現成答案，而是以一問一答的方式，一層層引導他看到自己過去忽略的慣性與模式。這樣的提問，讓他第一次能從旁觀者視角，看待自己的選擇與掙扎。"),_c('br'),_c('br'),_vm._v("\n    他特別提到幾個意外的轉念時刻：\n  ")]),_vm._v(" "),_c('article-paragraph',[_c('article-bold',[_vm._v("過去的自己總以「加法」方式驅動人生，想要更多、更好，卻忽略了「減法」也是選擇。")]),_vm._v("原來「沒有動力」不一定是壞事，它帶來了時間的鬆動、身體的修復與內在平靜，甚至是一種理想生活的樣貌。"),_c('br'),_c('br'),_vm._v("\n    他發現自己傾向選擇熟悉、安全的環境，但這樣的傾向，也不該被視為逃避，而是當下的自我選擇。他也從「問題」本身看見祝福——問題不再是需要立刻解決的麻煩，而是一種「幸福的狀態」，代表還擁有探索的自由。"),_c('br'),_c('br'),_vm._v("\n    整體而言，這場對話讓他從一種「對未來必須有答案」的焦慮中抽離，進入一種「當下允許自己慢慢來」的狀態。他學會了不是靠外界給答案，而是從內在慢慢釐清哪些是出於真心的想望。\n  ")],1),_vm._v(" "),_c('article-sub',[_vm._v("後續行動：允許慢下來的探索")]),_vm._v(" "),_c('article-paragraph',[_vm._v("\n    諮詢後，他提出幾項自己準備嘗試的行動方向：\n  ")]),_vm._v(" "),_c('article-paragraph',[_vm._v("\n    待在舒適圈深耕，觀察自己的變化：與其急著跨出，不如暫時接受當下的節奏，專注好好生活，並觀察內在的變化。"),_c('br'),_c('br'),_vm._v("\n    嘗試持續的小行動：設定小而穩定的目標，例如每週分享一支影片、重新建立生活儀式感，讓「動力」不是壓力來源，而是行動中自然生成的東西。不再急於定義工作身份，而是探索生活中的角色組合。"),_c('br'),_c('br'),_vm._v("\n    不強求用一句話定義自己現在的工作，而是允許多重身份同時存在——創作者、對話者、設計師、策展人，這些都可以交錯存在。"),_c('br'),_c('br'),_vm._v("\n    他也意識到，人生其實無需太快下定論。也許每段經歷都只是拼圖的一部分，重要的不是立刻拼出全貌，而是在拼的過程中，認出自己真正重視什麼。如果說職涯是一條路，那麼此刻的他，選擇先坐在路邊，靜靜地看著風景與方向牌——不急著走，反而看得更清楚。\n  ")]),_vm._v(" "),_c('article-consult-service'),_vm._v(" "),_c('article-about-sidd')],1)}
